* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 30px;
}
* {
  padding: 0;
  margin: 0;
  border: 0;
}
ul,
li {
  list-style: none;
}
img {
  vertical-align: top;
}
#search-form {
  width: 330px;
margin-right: auto;
margin-left: auto;
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  border-top: solid 5px blue;
  border-bottom: solid 5px yellow;
}
#search-form input {
  border-radius: 5px;
  padding: 5px;
}
.load-more{
  width: 120px;
  height: 40px;
  border-radius: 7px;
  background-color:blue;
  color: #fff;
  cursor: pointer;
  transition: all 300ms linear;
  box-shadow: ;
   border: none;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  transition: color 250ms linear, background-color 250ms linear, box-shadow 250ms linear;
  cursor: pointer;
}
.load-more:hover,
.load-more:focus{
  background-color:yellow;
  color: #212121;
}


.load-more:disabled {
  opacity: 0.7;
}

.is-hidden {
  display: none;
}
input {
   padding: 10px 20px;
}
button[type="submit"]{
 padding: 10px 20px;
 border-radius: 5px;
 border: none;
 cursor: pointer;
 transition: all 300ms linear;
 background-color: rgba(0, 238, 255, 0.89);
}
button[type="submit"]:hover,
button[type="submit"]:focus{
  background-color:gray;
}
.gallery{
  display:flex;
  flex-wrap: wrap;
  padding:30px;
  justify-content: center;
}
a{
  text-decoration: none;
  color: inherit;
  padding-bottom: 20px;
}
a:not(:last-child){
  padding-right: 20px;
}
a:visited,
a:hover {
  text-decoration: none;
}

.photo-card{
 background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  transition: transform 250ms linear;
  margin-bottom: 20px;
  cursor: pointer;
}
.photo-card:hover {
  transform: scale(1.07);
}
.thumb {
  width: 340px;
  height: 220px;
  object-fit: cover;
}
.img-content{
  display: block;
  width: 100%;
  height: 70%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.info{
  display: flex;
 justify-content: space-between;
  padding: 15px;
}

.info-item{
   display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
}
